import { requireNativeViewManager } from 'expo-modules-core';
import * as React from 'react';

export type ExpoMusicPickerViewProps = {
  name: string;
};

const NativeView: React.ComponentType<ExpoMusicPickerViewProps> =
  requireNativeViewManager('ExpoMusicPicker');

export default function ExpoMusicPickerView(props: ExpoMusicPickerViewProps) {
  return <NativeView name={props.name} />;
}
